addEventListener('fetch', event => {
    event.respondWith(handleRequest(event.request))
})

async function handleRequest(request) {
    const url = new URL(request.url);
    const path = url.pathname.split('/')[1];

    switch (path) {
        case 'menu':
            switch (request.method) {
                case 'GET':
                    return getMenuItems();
                case 'POST':
                    return addMenuItem(request);
                case 'DELETE':
                    return deleteMenuItem(request);
                default:
                    return new Response("Method Not Allowed", { status: 405 });
            }
        default:
            return new Response("Not Found", { status: 404 });
    }
}

async function getMenuItems() {
    try {
        const data = await MENU_ITEMS.get('menu_items', 'json');
        console.log("Data retrieved:", data);  // Debugging log
        return new Response(JSON.stringify(data || []), {
            headers: { 'Content-Type': 'application/json' }
        });
    } catch (error) {
        console.error("Failed to get menu items:", error);  // Error log
        return new Response("Internal Server Error", { status: 500 });
    }
}

async function addMenuItem(request) {
    try {
        const newItem = await request.json();
        let items = await MENU_ITEMS.get('menu_items', 'json') || [];
        newItem.id = items.length + 1;  // Simple ID assignment
        items.push(newItem);
        await MENU_ITEMS.put('menu_items', JSON.stringify(items));
        return new Response(JSON.stringify(newItem), {
            headers: { 'Content-Type': 'application/json' },
            status: 201
        });
    } catch (error) {
        console.error("Failed to add menu item:", error);  // Error log
        return new Response("Internal Server Error", { status: 500 });
    }
}

async function deleteMenuItem(request) {
    try {
        const { id } = await request.json();
        let items = await MENU_ITEMS.get('menu_items', 'json') || [];
        const index = items.findIndex(item => item.id === id);
        if (index === -1) {
            return new Response("Item not found", { status: 404 });
        }
        items.splice(index, 1);
        await MENU_ITEMS.put('menu_items', JSON.stringify(items));
        return new Response("Item deleted", { status: 204 });
    } catch (error) {
        console.error("Failed to delete menu item:", error);  // Error log
        return new Response("Internal Server Error", { status: 500 });
    }
}
